<template>
  <div>
    <LoadingSpinner v-if="isLoading" :size="100" />
    <section class="inbox">
      <PageHeading :text="`${userName}'s Inbox`" />
    </section>
  </div>
</template>

<script>
import LoadingSpinner from "../components/UI/LoadingSpinner";
import PageHeading from "../components/UI/PageHeading";

export default {
  name: "Inbox",
  components: {
    LoadingSpinner,
    PageHeading,
  },
  data() {
    return {
      isLoading: false,
    };
  },
  computed: {
    userName() {
      return this.$store.state.userName.split(".")[0];
    },
  },
};
</script>

<style lang="scss" scoped>
@import "../scss/variables";
h1 {
  color: $secondary;
  font-family: $font-secondary;
}
.inbox {
  padding: 10px 5px 5px 5px;
}
</style>
